















  import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class SelectBox extends Vue {
  @Prop()
  priority!: 'HIGH' | 'MEDIUM' | 'LOW'
  @Prop()
  isSelected!: boolean
  

  isOpen =  false;
  nowIsSelected = false;
  nowPriority = ''

  open() {
    this.isOpen = true
    this.$emit('open', this.isOpen)
  }

  select(priority: 'HIGH' | 'MEDIUM' | 'LOW') {
    this.nowPriority = priority
    this.isOpen = false
    this.nowIsSelected = true
    this.$emit('select', {
      priority: priority,
      isOpen: this.isOpen,
    })
  }

  clear() {
    this.isOpen = false
    this.nowIsSelected = false
  }

  reselect() {
    this.isOpen = true
    this.nowIsSelected = false
    this.$emit('reselect', this.isOpen)
  }

  priorityClass(priority: 'HIGH' | 'MEDIUM' | 'LOW'): string {
    switch(priority) {
      case 'HIGH':
        return 'highPriority'
      case 'MEDIUM':
        return 'mediumPriority'
      case 'LOW':
        return'lowPriority'
    }
  }
  created() {
    this.nowPriority = this.priority
    this.nowIsSelected = this.isSelected
  }
}
